<template>
  <div class="mineCom">
    <mineLi :liData=liData v-on:clickLi=clickLi :liArray=liArray></mineLi>
    <div class="flag" @click="flagFun()">{{flag?'üö©':'üò±'}}</div>
    <div class="input">
      <ul>
        <li><span>ÊñπÊ†ºÂÆΩÂ∫¶</span><input type="number" v-model="liData.liWidth"></li>
        <li><span>Ê®™Âêë‰∏™Êï∞</span><input type="number" v-model="liData.cross"></li>
        <li><span>Á´ñÂêë‰∏™Êï∞</span><input type="number" v-model="liData.vertical"></li>
        <li><span>Âú∞Èõ∑‰∏™Êï∞</span><input type="number" v-model="liData.mineNum"></li>
        <li><span>ÊóóÂ∏ú‰∏™Êï∞</span><span>{{flagNum}}</span></li>
      </ul>
      <button @click="gameBegin()">ÂºÄÂßã</button>
    </div>
  </div>
</template>

<script>
import mineLi from './mineLi'
export default {
  name: 'mineCom',
  data () {
    return {
       liData:{
        liWidth:30,
        liBorder:1,
        cross:10,
        vertical:10,
        mineNum:10,
      },
      liArray:[],
      flag:false,
      flagNum:0,
      allNum:0,
    }
  },
  components:{
    mineLi
  },
  methods:{
    gameBegin(){
      let allNum=this.liData.cross*this.liData.vertical;
      this.allNum=allNum;
      this.leftNum=allNum-this.liData.mineNum;
      let liArray=this.liArray;
      for (let i = 0; i < allNum; i++) {
        let a={
          isOpen:false,
          isMark:false,
          value:0
        };
        liArray.push(a);
      }

      let putNum=0;
      while(putNum < this.liData.mineNum){
        let pos=Math.floor(Math.random()*allNum);
        if(liArray[pos].value!==9){
          liArray[pos].value=9;
          putNum++;
        }
      }
      let cross=this.liData.cross;
      let initArray=liArray;
      for (let i = 0; i < allNum; i++) {
        if(liArray[i].value!==9){
          let yu=i%cross;
          console.log(allNum-cross);

          let li1,li2,li3,li4,li5,li6,li7,li8=undefined;
          if(yu!==0&&i>(cross-1)){li1=i-cross-1};
          if(i>(cross-1)){li2=i-cross;};
          if(yu!==(cross-1)&&i>(cross-1)){li3=i-cross+1;};
          if(yu!==0){li4=i-1;};
          if(yu!==(cross-1)){li5=i+1;};
          if(yu!==0&&i<allNum-cross){li6=i+cross-1;};
          if(i<allNum-cross){li7=i+cross;};
          if(yu!==(cross-1)&&i<allNum-cross){li8=i+cross+1;};
          let num=0;
          let newArray=initArray;
          li1?(newArray[li1].value==9?num++:null):null;
          li2?(newArray[li2].value==9?num++:null):null;
          li3?(newArray[li3].value==9?num++:null):null;
          li4?(newArray[li4].value==9?num++:null):null;
          li5?(newArray[li5].value==9?num++:null):null;
          li6?(newArray[li6].value==9?num++:null):null;
          li7?(newArray[li7].value==9?num++:null):null;
          li8?(newArray[li8].value==9?num++:null):null;
          liArray[i].value=num;
        }
      }
      this.liArray=liArray;
    },
    clickLi(e){
      let index=e.index;
      let liArray=this.liArray;
      let cross=this.liData.cross;
      if(index<0||index>allNum||liArray[index].isOpen){return}
      let allNum=this.liData.cross*this.liData.vertical;
      if(this.flag){
        if(liArray[index].isMark){
          liArray[index].isMark=false;
          this.flagNum--;
        }else{
          liArray[index].isMark=true;
          this.checkNum();
          this.flagNum++;
        }
      }else{
        if(liArray[index].isMark){return}
        if(liArray[index].value==9){
          //ÁÇπÂáªÂà∞Èõ∑ÔºåÊ∏∏ÊàèÁªìÊùü
          // this.leftNum=0;
          this.gameOver();
          return
        }
        if(liArray[index].value!==0){
          liArray[index].isOpen=true;
          this.checkNum();
          return
        }
          this.checkNum();
          liArray[index].isOpen=true
          let yu=index%cross;
          let e1={index:0};
          e1.index=index-cross;
          if(index>cross-1)
          this.clickLi(e1);
          e1.index=index+cross;
          if(index<allNum-cross)
          this.clickLi(e1);
          e1.index=index-1;
          if(yu!==0)
          this.clickLi(e1);
          e1.index=index+1;
          if(yu!==cross-1)
          this.clickLi(e1);
      }
    },
    flagFun(){
      this.flag=!this.flag;
    },
    gameOver(){
      let liArray=this.liArray;
					liArray.forEach(item=>{
            item.isOpen=true;
					})
				setTimeout(function(){
					alert('Ê∏∏ÊàèÂ§±Ë¥•ÔºÅ')
				},300)
    },
    checkNum(){
      let isAllMark=this.liArray.every(item=>{
          return (item.value==9&&item.isMark)||(item.isOpen&&item.value!=9)
        })
        if(isAllMark){
          console.log('ÊÅ≠ÂñúÈÄöÂÖ≥');

        }

    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .flag{
    float: left;
    margin-bottom: 10px;
  }
  .input{
    clear: both;
  }
  ul{
    padding: 0;
  }
  li{
    list-style: none;
    margin-bottom: 10px;
  }
  li:after{
    content:'';
    display:block;
    clear:both;
  }
  li span{
    margin-right:10px;
    float: left;
    line-height: 30px;
    font-size:30px;
  }
  li input{
    float: left;
    height: 30px;
    font-size:30px;
  }
  button{
    width: 300px;
    font-size:30px;
  }
</style>



